body {
  text-align: center;
  padding: 5%;
  background-color: #111;
  color: white;
  font-family: Arial;
}

.meme-button {
  cursor: pointer;
  font-style: italic;
  padding: 10px
}

.meme-item {
  text-align: left;
  display: inline-block;
  padding: 10px;
  position: relative;
  width: 200px;
  height: 200px;
  margin: 5px;
  cursor: pointer;
}
.my-meme-item {
  text-align: left;
  display: inline-block;
  padding: 10px;
  position: relative;
  width: 200px;
  height: 200px;
  margin: 5px;
  cursor: pointer;
  margin-bottom: 100px;
}
.meme-image {
  width: 200px;
  height: 200px;
  border-radius: 8px;
  border: 3px solid white;
  position: absolute;
  -o-object-fit: cover;
     object-fit: cover;
  -webkit-filter: brightness(90%);
          filter: brightness(90%);

}

.my-meme-image{
  position: relative;
  display: inline-block;
  margin: 5px;
  width: 200px;
  height: 200px;
  border-radius: 4px;
  border: 3px solid white;
}

.meme-text {
  width: 150px;
  text-align: center;
  position: absolute;
  bottom: 0;
  -webkit-animation-name: fade-in;
          animation-name: fade-in;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  font-size: 14px;
}

.download-text{
  font-size: 14px;
  text-align: center;
  width: inherit;
  position: absolute;
}
.no-txt {
  display: none;
}

.darken-img {
  background-color: grey;
  -webkit-filter: brightness(10%);
          filter: brightness(10%);
  -webkit-animation-name: darken;
          animation-name: darken;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
}

@-webkit-keyframes darken {
  from {-webkit-filter: brightness(90%);filter: brightness(90%)};
  to {-webkit-filter: brightness(10%);filter: brightness(10%)};
}

@keyframes darken {
  from {-webkit-filter: brightness(90%);filter: brightness(90%)};
  to {-webkit-filter: brightness(10%);filter: brightness(10%)};
}

@-webkit-keyframes fade-in {
  from {opacity: 0};
  to {opacity: 100}
}

@keyframes fade-in {
  from {opacity: 0};
  to {opacity: 100}
}



